$dark_green: rgb(96,120,72)
$green: rgb(120,144,72)
$light_green: rgb(192,216,96)
$cream: rgb(240,240,216)
$brown: rgb(96,72,72)

body
	color: black
	margin: 0
	background-color: white
	font-family: 'Open Sans', sans-serif
	-webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale

header, header *
	transition:  padding 0.5s, font-size 0.1s
	transition-delay: 0.6s

.menu_bar
	background-color: $cream
	position: fixed
	width: 100%
	top: 0
	box-shadow: 0 0 5px 0 black
	color: $green
	z-index: 3
	transform: translate3d(0, 0, 0)

.logo
	width: 30%
	display: block
	float: left
	padding: 0.5rem 0rem
	height: 4.5em

.menu 
	float: right
	display: block
	max-width: 60%
	height: 100%
	z-index: 11

.menu ul
	display: block
	padding: 0
	margin: 0
	float: right
	//height: 100%
	z-index: 10

.menu ul a
	float: left
	z-index: 4
	font-weight: 600
	font-size: 1.15rem
	display: block
	//height: 100%
	margin: 0.8rem 0
	padding: 1.1rem 1.2rem
	background-color: $cream
	box-sizing: border-box
	text-decoration: none
	color: $green
	border-width: 0px 0px 0px 1px;
	border-style: solid;
	border-color: $brown
	&:hover
		color: $cream
		transition: color 0.3s, background-color 0.3s, box-shadow 0.3s
		background-color: $brown
		box-shadow: inset 0px 0px 10px rgba(0,0,0,0.9)

.menu ul a li
	height: 100%
	list-style: none
	text-decoration: none


.large .container .menu ul a
	font-weight: 600
	font-size: 1.1rem
	//height: 100%
	padding: 2rem 1.4rem
	

.small .container .menu ul a
	font-size: 1rem
	padding: 1.2rem
	float: left
		
.container
	display: block
	position: relative
	box-sizing: border-box
	padding: 0 2rem
	max-width: 54em
	width: 100%
	margin: 0 auto

.carousel
	padding-top: 0em
	width: 100%

.offer
	width: 100%
	padding-top: 1.5em
	float: left
	position: relative
	z-index: 2
	color: white

.container60
	width: 75%
	float: left

.background_offer
	position: absolute
	overflow: hidden
	background-image: url(data:image/png;base64,)
	background-color: $brown
	box-shadow: inset 0px 0px 10px rgba(0,0,0,0.9)
	top: 1.5em
	left: 0
	right: 0
	bottom: 0
	z-index: 0

.background_offer div
	//box-sizing: border-box
	overflow: hidden
	position: relative
	height: 20.5%
	width: 33.333333%
	background-color: $green
	//border: 1px solid $dark_green
	float: left;
	z-index: 0;
	//box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.9)

div.full
	background-image: url(data:image/png;base64,)

div .half
	background-image: url(data:image/png;base64,)
	width: 16.6666666666%;

div .empty
	background-image: none
	background-color: transparent
	border: 0;
	box-shadow: 0px 0px 0px 0px rgba(0,0,0,0.9)

.offer .container
	padding-top: 2.5rem

.offer h2
	position: relative
	z-index: 2
	color: white

.offer ul
	float: left;
	margin-bottom: 4rem
	font-size: 1.4em
	line-height: 2em



.contact
	posistion: relative
	width: 100%
	float: left
	padding-top: 0em

.contact .container
	padding-bottom: 3rem

.carousel .container
	padding-top: 3.5rem

.slick_carousel
	float: left
	display: block
	width: 100%

h2
	display: block
	float: left
	width: 100%
	margin: 3rem 0rem 2rem 0rem
	font-size: 2.4rem
	line-height: 1.3em
	color: $green	

.contact img
	margin-top: 0.3em
	margin-right: 0.5em
	height: 1em

.contact p
	font-size: 1.5em
	line-height: 1.5em

.footer
	background-image: url(data:image/png;base64,) 
	background-color: $brown
	border-top: 1.5em solid $green
	color: white
	text-align: center
	width: 100%
	font-size: 0.9em
	font-style: italic
	float: left
	padding: 1em

.footer a
	color: white
	text-decoration: none

.footer a:hover
	text-decoration: underline